.sobre-festival{
    padding: $separacion 0 3rem 0;
    @include tablet{
        @include grid(2, $separacion);
        align-items: center;
    }
    @include desktop{
        padding: $separacion 0 5rem 0;
    }
}

.contenido-festival{
    h2{
        font-size: 3rem;
    }
    .fecha{
        color: $rosa;
        font-weight: bold;
    }
    .texto{
        //font-size: 1rem;
        line-height: 1.5;//interlineado
    }
    #lineup-pivot{
        margin: 0;
    }
}

.imagen{
    @include desktop{
        #lineup-pivot{
            margin: 1rem 0;
        }
    }
}