@use "sass:math";

.header{
    background-color: $verde;
    .contenido-header{
        padding-top: math.div($separacion, 5);
        //margin: 0;
        a{
            font-size: 1.5rem;
        }
        
        @include tablet{
            display: flex;
            justify-content: space-between;
            align-items: center;
            padding: math.div($separacion, 5);
        }
    }

    h1{
        color: $blanco;
        margin-bottom: 0.5rem;
        text-align: center;
        font-size: 2rem;
        @include tablet{
            margin-bottom: 0rem;
            font-size: 3rem;
        }
        @include desktop{
            font-size: 4rem;
        }
        span{
            font-size: 1.2rem;
            font-weight: lighter;
            text-align: initial;
        }
    }

    

    &.fijo{
        position: fixed;
        width: 100%;
        box-shadow: 0 .3rem .5rem #444;
        z-index: 2;
    }

}

.navegacion-principal{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    //column-gap: 2rem;
    
    a{
        padding: 0.5rem;
        color: $blanco;
        font-size: 2rem;
        text-align: center;
        &:hover {
            color: $amarillo;
        }
    }
} 