@use 'sass:math';

.boletos{
    padding: 2.5rem 0;
    h3{
        text-align: center;
        color: $blanco;
    }
    
    .contenedor-boletos{
        @include grid(2, $separacion)
    }

    .plan{
        p{
            color: $blanco;
            font-size: 3rem;
            text-align: center;
            margin-bottom: 2rem;
        }
    }

    .tabla-pase{
        width: 100%;
        border-spacing: 0;
        thead{
            background-color: $rosa;
            th{
                padding: 1rem;
                color: $blanco;
                text-transform: uppercase;
                font-weight: $delgada;
                font-size: 2rem;
            }
        }
        tbody{
            background-color: $blanco;
            td{
                padding: math.div($separacion, 5);
                border-bottom: 1px solid #e1e1e1;
            }
            tr{
                &:last-of-type{
                    td{
                        border: none;
                    }
                }
            }
        }
        tfoot{
            background-color: $morado;
            td{
                text-align: center;
                font-size: 3rem;
                font-weight: $delgada;
                color: $blanco;
                padding: math.div($separacion, 3);
            }
            
        }
    }
}