@use "sass:math";
.galeria{
    padding: $separacion 0;
    
    h3{
        text-align: center;
        padding-top: 2rem;
    }
}

.galeria-imagenes{
    //lineas para eliminar estilos default
    padding: 0;
    list-style: none;

    margin-top: $separacion;
    @include grid(2, $separacion);
    
    @include tablet{
        @include grid(4, math.div($separacion, 2));
    }
    @include desktop{
        @include grid(6, math.div($separacion, 2));
    }
    li{
        margin: auto auto;
    }
    li:hover{
        cursor: pointer;
    }
}



.overlay{
    position: fixed;
    background-color: rgba($color: #000000, $alpha: 0.5);
    width: 100%;
    height: 100%;
    top: 0;
    display: grid;
    place-items: center;
    //edit
    z-index: 3;
    img{
        max-width: 58%;
        
    }
}

.btn-cerrar{
    margin: 0;
    width: 3rem;
    height: 3rem;
    background-color: $blanco;
    display: grid;
    place-items: center;
    font-weight: $negro;
    font-size: 2rem;
    border-radius: 50%;

    &:hover{
        cursor: pointer;
    }
}