@use "sass:math";

.lineup{
    background-color: $rosa;
    padding: math.div($separacion,2) 0;
    h3, p{
        text-align: center;
        color: $blanco;
    }
    h3{
        padding-bottom: math.div($separacion,2);
    }
    .dia{
        text-transform: uppercase; 
    }
}

//poner 4 columnas en vista laptop+
.desktop-alinear{
    margin: 0 math.div($separacion, 3) ;
    @include desktop{
        @include grid(2, $separacion);
        .desktop-columnas{
            background-color: darken($rosa, 5);
            padding-bottom: math.div($separacion, 2);
        }
    }
}

.escenarios-contenedor{
    @include tablet{
        @include grid(2, $separacion)
    }
    
    .escenario{
        padding: math.div($separacion, 2) 0;
    }
    
    .nombre-escenario{
        color: $negro;
        font-size: 2rem;
        font-weight: $delgada;

        &:after{
            content: '';
            width: 20rem;
            height: 10rem;
            display: block;
            margin: 2rem auto 0 auto;
            //el background-size controla el tamaño de la foto que se muestra
            background-size: 15rem;
            background-repeat: no-repeat;
            background-position: center center;
        }
    }
}

.regueton .nombre-escenario{
    &:after{
        background-image: url(../img/icono_edm.png);
    }
}

.rock{
    margin-bottom: 2rem;
    @include tablet{
        margin-bottom: 0;
    }
}

.rock .nombre-escenario{
    &:after{
        background-image: url(../img/icono_rock.png);
    }
}

.calendario{
    padding: 0;
    list-style: none;
    li{
        padding: math.div($separacion, 3);
        font-weight: $bold;
        span{
            font-weight: $regular;
        }
    }
}

//selector para hacer una fila si y otra no
.bg-naranja .calendario li:nth-child(odd){
    background-color: darken($verde, 10);
}

.bg-morado .calendario li:nth-child(odd){
    background-color: darken($amarillo, 15);
}







